.Grogu_wrap{
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 50px;
    margin: 0 auto;
    font-size: 1.5rem;
    color: rgb(255, 242, 0);
}
.Grogu_photo{
    display: flex;
    width: 100%;
    max-width: 350px;
    border-radius: 15px;
}
.Grogu_photo img{
    width: 100%;
    border-radius: 15px;
}
.Grogu_info{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 500px;
}
.Grogu_info h2{
    font-size: 2rem;
    width: 100%;
    max-width: 200px;
}
.Grogu_info p{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}