.photo_wrap{
    display: flex;
    width: 100%;
    width: 1100px;
    flex-wrap: wrap;
    margin: 20px auto;
    gap: 10px;
}
.photo_block{
    display: flex;
    width: 100%;
    max-width: 330px;
    max-height: 285px;
}
.photo_block img{
    width: 100%;
    max-width: 300px;
}