button{
    cursor: pointer;
  }
  a{
    color: inherit;
    text-decoration: none;
  }
  .Main{
    display: flex;
    background-color:rgb(18, 109, 109);
    max-width: 930px;
    padding: 10px;
    margin: 0 auto;
    border-radius: 15px;
  }
  .user_info{
    display: flex;
    align-items: start;
    justify-content: space-around;
    flex-direction: column;
    color: aliceblue;
    margin-bottom: 15px;
  }
  .span_content{
   
    margin-bottom: 25px;
  }
  .user_info__wrap{
    display: flex;
    gap: 15px;
  }
  .autor_wrap{
    display: flex;
    gap: 15px;
    align-items: center;
    justify-content: space-around;
    
  }
  .autor_wrap svg{
    display: flex;
    width: 15px;
    height: 15px;
  }
  .User-img {
    margin-right: 20px;
  }
  .User-img img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
  .rey_photo img{
    max-width: 650px;
    height: 350px;
    border-radius: 15px;
  }
  .rey_photo {
    width: 100%;
  }
  .btn {
    width: 60px;
    height: fit-content;
    gap: 10px;
    background: inherit;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .btn svg{
    width: 15px;
    height: 15px;
    background: inherit;
  }
  .btn_wrap{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-around;
  }
  .follow:active svg path{
    transform: scale(1.2);
    fill: rgb(255, 0, 0);
  }
  .comments:active svg path{
    transform: scale(1.2);
    fill: rgb(255, 0, 0);
   
  }
  .wrap{
    margin-bottom: 15px;
    border: 1px solid #fff;padding: 10px;
    border-radius: 15px;
  }